.mainTitle {
    font-size: 100px;
}

.cap {
    text-transform: capitalize;
    color: #53c28b;
}

.item {
    display: flex;
    gap: 50px;
    margin-top: 50px;
    margin-bottom: 100px;
}

.item:nth-child(2n) {
    flex-direction: row-reverse;
}

.imgContainer {
    flex: 1;
    width: 350px;
    height: 500px;
    position: relative;
}

.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
}

.title {
    font-size: 50px;
}

.desc {
    font-size: 20px;
}

@media only screen and (max-width: 426px) {
    .mainTitle {
        font-size: 4.7rem;
    }

    .imgContainer {
        width: 18rem;
    }

    .imgContainer .img {
        width: 100%;
        object-fit: cover;
    }

    .item {
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 2rem;
    }

    .item:nth-child(2n) {
        flex-direction: column-reverse;
    }
}

@media only screen and (max-width: 321px) {
    .imgContainer {
        width: 15rem;
    }
}